___CSS_LOADER_EXPORT___.push([module.id, `.main-content {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    position: relative;
    justify-content: center;
    gap: 32px;
    margin: 11px auto;
}

.right-blocks {
    display: flex;
    flex-direction: column;
    gap: 32px;
}

@keyframes pulse {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 0.7;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.main-content__icon {
    position: fixed;
    right: 20px;
    bottom: 20px;
    cursor: pointer;
    z-index: 1000;
    animation: pulse 2s infinite ease-in-out;
}

@media only screen and (max-width: 1119px) {
    .main-content {
        flex-direction: column;
        align-items: center;
    }

    .top-right-block {
        align-items: center;
    }

    .right-blocks {
        flex-direction: column-reverse;
        align-items: center;
        gap: 32px;
    }
}

@media only screen and (max-width: 767px) {
    .main-content {
        flex-direction: column;
        align-items: center;
    }

    .top-right-block {
        align-items: center;
    }
}

