___CSS_LOADER_EXPORT___.push([module.id, `@keyframes fillAndShrinkStatusBar {
    0% {
        width: 0;
    }
    50% {
        width: 100%;
    }
}

.status-bar {
    display: flex;
    flex-direction: column;
}

.status-bar__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 3px;
}

.status-bar__count {
    font-size: 16px;
    color: #333;
    margin-bottom: 8px;
}

.status-bar__track {
    width: 100%;
    height: 8px;
    background-color: #e0e0e0;
    border-radius: 4px;
    overflow: hidden;
}

.status-bar__progress {
    height: 100%;
    animation: fillAndShrinkStatusBar 2s ease forwards;
}

.status-bar__progress--blue {
    background-color: #415DE8;
    border-radius: 4px;
}

.status-bar__progress--red {
    background-color: #FF456A;
    border-radius: 4px;
}

.status-bar__title {
    font-size: 16px;
    color: #222B55;
    margin-bottom: 8px;
}

.status-bar__count, .status-bar__subtitle {
    font-size: 16px;
    color: #222B55;
    margin-bottom: 8px;
}

.status-bar__unlimited {
    color: #7B829A;
    font-size: 14px;
    line-height: 14px;
}

.status-bar__total {
    color: #7B829A;
}

.status-bar__subtitle {
    margin-top: 8px;
    color: #7B829A;
    text-align: start;
    font-size: 14px;
    line-height: 20px;
}

.upgrade-link {
    color: #7B829A;
}




