___CSS_LOADER_EXPORT___.push([module.id, `.button {
    width: 135px;
    height: 32px;
    padding: 8px 16px 8px 13px;
    border-radius: 8px;
    gap: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #415DE8;
    letter-spacing: 0;
    text-align: center;
    border: none;
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease;
    font-size: 14px;
    font-family: 'Lato', sans-serif;
}

.button--small {
    width: 85px;
}

.button--large {
    width: 165px;
}

.button--text-blue {
    color: var(--royal-blue);
}

.button--text-white {
    color: var(--pure-white);
}

.button--light-royal-12 {
    background-color: var(--light-royal-12);
}

.button--salmon-pink {
    background-color: var(--salmon-pink);
}

.button--royal-blue {
    background-color: var(--royal-blue);
}`, "",{"version":3,"sources":["webpack://./src/components/Button/Button.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,YAAY;IACZ,0BAA0B;IAC1B,kBAAkB;IAClB,QAAQ;IACR,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,yBAAyB;IACzB,iBAAiB;IACjB,kBAAkB;IAClB,YAAY;IACZ,eAAe;IACf,aAAa;IACb,sCAAsC;IACtC,eAAe;IACf,+BAA+B;AACnC;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,mCAAmC;AACvC","sourcesContent":[".button {\n    width: 135px;\n    height: 32px;\n    padding: 8px 16px 8px 13px;\n    border-radius: 8px;\n    gap: 8px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #415DE8;\n    letter-spacing: 0;\n    text-align: center;\n    border: none;\n    cursor: pointer;\n    outline: none;\n    transition: background-color 0.3s ease;\n    font-size: 14px;\n    font-family: 'Lato', sans-serif;\n}\n\n.button--small {\n    width: 85px;\n}\n\n.button--large {\n    width: 165px;\n}\n\n.button--text-blue {\n    color: var(--royal-blue);\n}\n\n.button--text-white {\n    color: var(--pure-white);\n}\n\n.button--light-royal-12 {\n    background-color: var(--light-royal-12);\n}\n\n.button--salmon-pink {\n    background-color: var(--salmon-pink);\n}\n\n.button--royal-blue {\n    background-color: var(--royal-blue);\n}"],"sourceRoot":""}]);
