___CSS_LOADER_EXPORT___.push([module.id, `.header {
    padding: 0 32px;
    height: 92px;
    display: flex;
    align-items: center;
}

.header__logo {
    flex: 0 0 calc((100% - 1120px) / 2);
    padding-bottom: 1px;
}

.header__actions {
    width: 100%;
    display: flex;
    position: relative;
    transition: opacity 0.3s ease, visibility 0s linear 0.3s;
    opacity: 1;
    visibility: visible;
}

.header__menu {
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.header__menu:hover {
    background-color: #e0e3f4;
    border-radius: 4px;
}

.header__menu-icon {
    transition: transform 0.3s ease;
}

.header__menu-links {
    display: flex;
    gap: 40px;
    align-items: center;
    margin: 1px 0 3px 1px;
    width: 584px;
}

.header-menu__link {
    display: inline-block;
    font-size: 18px;
    color: #222B55;
    transition: .15s ease-in-out;
    text-decoration: none;
    white-space: nowrap;
}

.header-menu__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 188px;
    height: 24px;
}

.header__button-icon-link {
    margin-left: 7px;
    white-space: nowrap;
    color: #222B55;
    text-decoration: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 100%;
}

.action-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    cursor: pointer;
    width: 340px;
    gap: 33px;
    margin: -2px 0 1px 0;
}

.company-selector {
    display: flex;
    align-items: center;
    -webkit-user-select: none;
}

.company-selector:hover {
    background-color: var(--light-salmon);
    border-radius: 8px;
}

.selector__icon {
    width: 32px;
    height: 32px;
    border-radius: 12px;
    background-color: #FFC2C2;
    color: #FF5762;
    display: flex;
    justify-content: center;
    align-items: center;
}

.selector__title {
    display: block;
    margin-left: 9px;
    margin-right: 7px;
    font-size: 18px;
    text-transform: uppercase;
    white-space: nowrap;
}

.selector__toggle {
    margin-left: auto;
    padding: 8px;
    display: flex;
    align-items: center;
    transition: transform 0.3s ease;
}

.selector__toggle.toggled {
    transform: rotate(180deg);
}

.dropdown-menu, .dropdown-menu-actions {
    position: absolute;
    top: 124%;
    right: 0;
    min-width: 210px;
    max-width: 320px;
    background-color: #FFFFFF;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0,0,0,.06), 0 10px 16px rgba(0,0,0,.06);
    z-index: 8900;
    padding: 10px 0;
    transition: transform 0.3s ease;
    opacity: 0;
    visibility: hidden;
    max-height: 0;
    overflow: hidden;
}

.dropdown:hover .dropdown-menu {
    display: block;
    visibility: visible;
    opacity: 1;
}


.company-selector.active .dropdown-menu {
    display: block;
    opacity: 1;
    visibility: visible;
    max-height: 500px;
    transition-delay: 0s;
}

.header__menu.active .dropdown-menu__actions {
    display: block;
    opacity: 1;
    visibility: visible;
    max-height: 500px;
    transition-delay: 0s;
}

.menu__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.menu__item {
    display: flex;
    align-items: center;
    white-space: nowrap;
    gap: 7px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 16px;
    line-height: 24px;
}

.menu__item:hover {
    background-color: var(--light-navy-8);
}

/*  -        1119px */
@media only screen and (min-width: 1120px) {
    .header__menu {
        display: none; /*  - */
    }
    .header__menu-links {
        display: flex; /*     */
    }
}

/*     768px  1119px */
@media only screen and (max-width: 1119px) {
    .header__menu-links, .action-bar .action-bar__button {
        display: none;
    }
    .header__menu {
        display: flex;
    }
    .selector__title {
        display: none;
    }
}

@media only screen and (max-width: 767px) {
    .header__menu {
        display: flex;
    }
    .selector__title, .action-bar .action-bar__button {
        display: none;
    }
}



@media (max-width: 1124px) {
    .header__menu-links {
        display: none;
    }

    .header__menu {
        display: flex;
    }

    .company-selector, .header__logo {
        margin-left: auto;
        justify-content: flex-end;
        align-items: center;
    }

    .dropdown-menu__actions {
        position: absolute;
        min-width: 210px;
        max-width: 320px;
        background-color: #FFFFFF;
        border-radius: 8px;
        box-shadow: 0 2px 6px rgba(0,0,0,.06), 0 10px 16px rgba(0,0,0,.06);
        z-index: 8900;
        padding: 10px 0;
        opacity: 0;
        visibility: hidden;
        max-height: 0;
        overflow: hidden;
    }

    .header__menu.active {
        display: block;
        opacity: 1;
        visibility: visible;
        max-height: 500px;
    }
}

